import Vue from 'vue'
import Router from 'vue-router'
import Hello from '@/components/Hello'
import popup from '@/components/popup'
import cells from '@/components/cells'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Hello',
      component: Hello
    },
    {
      path: '/popup',
      name: 'popup',
      component: popup
    },
    {
      path: '/cells',
      name: 'cells',
      component: cells
    }
  ]
})



